listado-servicios {
  @media (min-width: 768px) {
    display: grid (2, 1fr) {
      display: grid;
      gap: 5rem;
      grid-template-columns: repeat(2, 1fr);
    }
  }

  .servicio {
    margin-bottom: 2rem;
    background-color: #fff;
    padding: 5rem / 2;
    border-radius: 1rem;

    @media (min-width: 768px) {
      margin-bottom: 0;
    }

    p {
      margin: 0 0 5rem / 4 0;
    }

    .nombre-servicio {
      color: black;
    }
    .precio-servicio {
      color: blue;
      margin: 0;
      font-size: 3rem;
      font-weight: bold;
    }

    &.seleccionado {
      background-color: blue;

      p {
        color: #fff;
      }
    }
  }
}
